import Vue from "vue";
import App from "./App.vue";
import store from "./store";
import router from "./router";
import Confirm from "@/plugins/confirm";
import Alert from "@/plugins/alert";

Vue.config.productionTip = true;

Vue.use(Confirm);
Vue.use(Alert);

new Vue({
  store,
  router,
  render: h => h(App)
}).$mount("#app");
