import Vue from "vue";
import Vuex from "vuex";
import * as firebase from "firebase/app";
import "firebase/firestore";
import "firebase/auth";

Vue.use(Vuex);

const firebaseConfig = {
  apiKey: process.env.VUE_APP_FIREBASE_API_KEY,
  authDomain: process.env.VUE_APP_FIREBASE_AUTH_DOMAIN,
  databaseURL: process.env.VUE_APP_FIREBASE_DB_URL,
  projectId: process.env.VUE_APP_FIREBASE_PROJECT_ID,
  storageBucket: process.env.VUE_APP_FIREBASE_STORAGE_BUCKET,
  messagingSenderId: process.env.VUE_APP_FIREBASE_MESSAGING_SENDER_ID
};

// Initialize Firebase
firebase.initializeApp(firebaseConfig);

const db = firebase.firestore();

export default new Vuex.Store({
  state: {
    userLoggedIn: false,
    currentUser: {
      providerId: "",
      uid: "",
      displayName: "",
      email: "",
      photoURL: ""
    },
    notes: []
  },
  methods: {},
  getters: {
    isUserLoggedIn(state) {
      return state.userLoggedIn;
    },
    getCurrentUser(state) {
      return state.currentUser;
    },
    getNotes(state) {
      return state.notes;
    }
  },
  mutations: {
    checkAuthState(state) {
      firebase.auth().onAuthStateChanged(user => {
        if (user) {
          state.userLoggedIn = true;
          state.currentUser.providerId = user.providerId;
          state.currentUser.uid = user.uid;
          state.uid = user.uid;
          state.currentUser.displayName = user.displayName;
          state.currentUser.email = user.email;
          state.currentUser.photoURL = user.photoURL;
        }
      });
    },
    setNotes(state, userNotes) {
      if (userNotes !== undefined) {
        state.notes = userNotes;
      }
    },
    addNote(state, newNote) {
      state.notes.unshift(newNote);
      var usersRef = db.collection("users");
      usersRef.doc(state.currentUser.uid).set({
        notes: state.notes
      });
    },
    deleteNote(state, noteId) {
      state.notes.forEach(function(note, index) {
        if (note.id === noteId) {
          state.notes.splice(index, 1);
          localStorage.setItem("notes", JSON.stringify(state.notes));
        }
      });
    },
    saveNote(state, savedNote) {
      state.notes.forEach(function(note, index) {
        if (note.id === savedNote.id) {
          state.notes.splice(index, 1, savedNote);
          var usersRef = db.collection("users");
          usersRef.doc(state.currentUser.uid).set({
            notes: state.notes
          });
        }
      });
    }
  },
  actions: {
    // Begin create user
    async createUser(context, { userEmail, userPassword }) {
      await firebase
        .auth()
        .createUserWithEmailAndPassword(userEmail, userPassword)
        .then(() => {
          var user = firebase.auth().currentUser;
          var usersRef = db.collection("users");
          if (user) {
            usersRef.doc(user.uid).set({
              notes: []
            });
          }
        });
    },
    // Begin sign in user
    async signInUser(context, { userEmail, userPassword }) {
      await firebase
        .auth()
        .signInWithEmailAndPassword(userEmail, userPassword)
        .then(() => {
          context.state.userLoggedIn = true;
        });
    },
    // Begin sign out user
    async signOutUser(context) {
      await firebase
        .auth()
        .signOut()
        .then(function() {
          context.state.userLoggedIn = false;
          console.log("Вы успешно вышли.");
        })
        .catch(error => {
          console.log(error);
        });
    },

    // Begin update profile user
    async updateProfileUser(context, { userName, userPhotoURL }) {
      await firebase
        .auth()
        .currentUser.updateProfile({
          displayName: userName,
          photoURL: userPhotoURL
        })
        .then(() => {
          context.commit("checkAuthState");
        })
        .catch(error => {
          console.log(error);
        });
    },
    // Begin update email user
    async updateEmailUser(context, { userEmail }) {
      await firebase
        .auth()
        .currentUser.updateEmail(userEmail)
        .then(() => {
          context.commit("checkAuthState");
        });
    },

    userNotes(context) {
      firebase.auth().onAuthStateChanged(async user => {
        if (user) {
          await db
            .collection("users")
            .doc(user.uid)
            .get()
            .then(doc => {
              if (doc.exists) {
                context.commit("setNotes", doc.data().notes);
              } else {
                console.log("No such document!");
              }
            })
            .catch(error => {
              console.log("Error getting document:", error);
            });
        }
      });
    }
  },
  modules: {}
});
